import React, { useEffect, useRef } from 'react';
import { Box } from '@mui/material';

import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import BrandVideo from 'assets/video/aiPromotion/brand-video.mp4';
// 썸네일 추후 변경될 수 있음
// import VideoThumnail from 'assets/images/aiPromotion/img-video-thumnail.png';

import { AIBrandVideoContainer } from '../styles';

gsap.registerPlugin(ScrollTrigger);

export default function AIBrandVideo() {
  const containerRef = useRef(null);
  const videoBoxRef = useRef(null);
  const videoRef = useRef<HTMLVideoElement | null>(null);

  useEffect(() => {
    const ctx = gsap.context(() => {
      gsap.to(videoBoxRef.current, {
        width: '100%',
        height: '100%',
        borderRadius: '0',
        ease: 'power3.out',
        scrollTrigger: {
          trigger: containerRef.current,
          start: 'top top',
          end: '+=300%',
          scrub: true,
          pin: true,
          onEnter: () => {
            videoRef.current?.play();
          },
          onLeaveBack: () => {
            videoRef.current?.pause();
          },
        },
      });
    }, containerRef);

    return () => ctx.revert();
  }, []);

  return (
    <AIBrandVideoContainer ref={containerRef}>
      <Box ref={videoBoxRef}>
        <video ref={videoRef} muted playsInline>
          <source src={BrandVideo} type="video/mp4" />
        </video>
      </Box>
    </AIBrandVideoContainer>
  );
}
