import React, { forwardRef } from 'react';
import { FormattedMessage } from 'react-intl';

import { Box, Link } from '@mui/material';
import LogoWhite from 'assets/images/aiPromotion/img-logo-white.svg';
import LogoColor from 'assets/images/aiPromotion/img-logo-color.svg';

import { AIHeaderContainer } from '../styles';
import { Props } from '../styles.type';

const AIHeader = forwardRef<HTMLElement, Props>(({ isInverted = false }, ref) => {
  return (
    <AIHeaderContainer ref={ref} className={isInverted ? 'inverted' : ''}>
      <Box>
        <Link href="#subscribe">
          <Box>
            <img src={isInverted ? LogoColor : LogoWhite} alt="Hancom Docs Logo" />
          </Box>
        </Link>
        <Box component="nav">
          <Link href="https://www.hancomdocs.com/ko/plans" target="_blank" rel="noopener noreferrer">
            <FormattedMessage id="HEADER-SUBSCRIBE" />
          </Link>
          <Link href="https://support.hancomdocs.com/" target="_blank" rel="noopener noreferrer">
            <FormattedMessage id="HEADER-SUPPORT" />
          </Link>
          <Link
            href="https://support.hancomdocs.com/09ef252f-1e48-4702-90a8-d9cb93dc1fba"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FormattedMessage id="HEADER-EVENT" />
          </Link>
        </Box>
      </Box>
    </AIHeaderContainer>
  );
});

export default AIHeader;
