import React from 'react';

import { FormattedMessage } from 'react-intl';
import RefinderMockup from 'assets/images/aiPromotion/img-refinder-ai-mockup.png';
import { Typography, Box } from '@mui/material';

import { AIRefinderContainer, AICard, AISectionPaddingLarge, AISectionContainer, AIContentsContainer } from '../styles';

export default function AIRefinder() {
  return (
    <AIRefinderContainer>
      <AISectionPaddingLarge>
        <AISectionContainer>
          <AICard>
            <Typography variant="h6" fontWeight={600} data-aos="fade-up" data-aos-delay="0">
              <FormattedMessage id="REFINDER-TITLE" />
            </Typography>

            <Typography variant="h4" fontWeight={700} color="#000" data-aos="fade-up" data-aos-delay="100">
              <FormattedMessage id="REFINDER-HEADING" />
            </Typography>

            <Typography
              variant="subtitle2"
              component="p"
              fontWeight={400}
              color="#333"
              data-aos="fade-up"
              data-aos-delay="200"
            >
              <FormattedMessage id="REFINDER-DESCRIPTION" />
            </Typography>

            <AIContentsContainer data-aos="fade-up" data-aos-delay="300">
              <Box>
                <img src={RefinderMockup} alt="" width="100%" />
              </Box>
            </AIContentsContainer>
          </AICard>
        </AISectionContainer>
      </AISectionPaddingLarge>
    </AIRefinderContainer>
  );
}
