import React, { useEffect, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { FormattedMessage } from 'react-intl';

import { Application } from '@splinetool/runtime';

import { Box, Typography } from '@mui/material';
import { AIBrandStoryContainer, AIBrandStoryTextContainer, BackDrop } from '../styles';

gsap.registerPlugin(ScrollTrigger);

const formatTags = {
  br: () => <br />,
  purple: (chunks: React.ReactNode) => <span className="highlight-purple">{chunks}</span>,
  negative: (chunks: React.ReactNode) => <span className="negative">{chunks}</span>,
};

export default function AIBrandStory() {
  const containerRef = useRef<HTMLDivElement>(null);
  const canvasRef = useRef<any>(null);
  const splineAppRef = useRef<Application | null>(null);

  useEffect(() => {
    const loadSpline = async () => {
      if (canvasRef.current) {
        const app = new Application(canvasRef.current);
        await app.load('https://prod.spline.design/trwpwC9XrLh0wt6J/scene.splinecode');
        splineAppRef.current = app;
      }
    };

    loadSpline();

    const ctx = gsap.context(() => {
      const textWrap = document.querySelector('.ai-brand-text') as HTMLElement;
      const backdrop = document.querySelector('.backdrop') as HTMLElement;

      gsap.to(textWrap, {
        y: '-300vh',
        ease: 'none',
        scrollTrigger: {
          trigger: containerRef.current,
          start: 'top top',
          end: '+=400%',
          scrub: true,
          pin: true,
        },
      });

      gsap.to(backdrop, {
        opacity: 1,
        ease: 'none',
        scrollTrigger: {
          trigger: containerRef.current,
          start: 'top top',
          end: '+=400%',
          scrub: true,
        },
      });

      ScrollTrigger.create({
        trigger: containerRef.current,
        start: 'top top',
        end: '+=400%',
        scrub: true,
        onUpdate: ({ progress }) => {
          const cam = splineAppRef.current?.findObjectByName('Camera') as any;
          if (cam) {
            cam.position.z = 1600 + 1400 * progress;
          }
        },
      });
    }, containerRef);

    return () => ctx.revert();
  }, []);

  return (
    <AIBrandStoryContainer ref={containerRef}>
      <AIBrandStoryTextContainer className="ai-brand-text">
        <Box className="ai-story-item">
          <Typography variant="h2" fontWeight={700} color="#fff">
            <FormattedMessage id="AIBRAND_1" values={formatTags} />
          </Typography>
        </Box>

        <Box className="ai-story-item">
          <Typography variant="h2" fontWeight={700} color="#fff">
            <FormattedMessage id="AIBRAND_2" values={formatTags} />
          </Typography>
        </Box>

        <Box className="ai-story-item">
          <Typography variant="h2" fontWeight={700} color="#fff">
            <FormattedMessage id="AIBRAND_3" values={formatTags} />
          </Typography>
        </Box>

        <Box className="ai-story-item">
          <Typography variant="h2" fontWeight={700} color="#fff">
            <FormattedMessage id="AIBRAND_4" values={formatTags} />
          </Typography>
        </Box>
      </AIBrandStoryTextContainer>
      <BackDrop className="backdrop" />
      <canvas ref={canvasRef} style={{ width: '100%', height: '100%' }} />
    </AIBrandStoryContainer>
  );
}
