import React from 'react';
import { Typography } from '@mui/material';
import { FormattedMessage } from 'react-intl';

import MockupSlider from './MockupSlider';
import { AIVarientDocumentContainer, AITitleContainer, AISectionPaddingLarge } from '../styles';

const formatTags = {
  br: () => <br />,
};

export default function AIVarientDocument() {
  return (
    <AIVarientDocumentContainer>
      <AISectionPaddingLarge>
        <AITitleContainer sx={{ padding: '0 2rem' }}>
          <Typography
            variant="h2"
            textAlign="center"
            fontWeight={700}
            color="#8070E4"
            data-aos="fade-up"
            data-aos-delay="0"
          >
            <FormattedMessage id="VARIENT-TITLE-1" />
          </Typography>
          <Typography
            variant="h2"
            textAlign="center"
            fontWeight={700}
            color="#000"
            data-aos="fade-up"
            data-aos-delay="100"
          >
            <FormattedMessage id="VARIENT-TITLE-2" />
          </Typography>
          <Typography
            variant="subtitle2"
            fontWeight={500}
            color="#333"
            textAlign="center"
            data-aos="fade-up"
            data-aos-delay="200"
          >
            <FormattedMessage id="VARIENT-DESCRIPTION" values={formatTags} />
          </Typography>
        </AITitleContainer>
        <div data-aos="fade-up" data-aos-delay="300">
          <MockupSlider />
        </div>
      </AISectionPaddingLarge>
    </AIVarientDocumentContainer>
  );
}
