import React from 'react';
import { Box, Typography } from '@mui/material';
import { FormattedMessage } from 'react-intl';

import VisualVideo from 'assets/video/aiPromotion/video-hero.mp4';
import LogoVisual from 'assets/images/aiPromotion/img-visual-logo.svg';

import { AIHeroContainer, ScrollLine } from '../styles';

export default function AIHero() {
  return (
    <AIHeroContainer>
      <Box component="figure">
        <video muted autoPlay loop playsInline>
          <source src={VisualVideo} type="video/mp4" />
        </video>
      </Box>
      <Box component="article">
        <Typography variant="h4" textAlign="center" fontWeight={600} data-aos="fade-up" data-aos-delay="0">
          <FormattedMessage id="HERO-TITLE" />
        </Typography>
        <Box component="div" data-aos="fade-up" data-aos-delay="200">
          <img src={LogoVisual} alt="" />
        </Box>
      </Box>
      <ScrollLine data-aos="fade-up" data-aos-delay="400">
        <Typography component="p">
          <FormattedMessage id="HERO-SCROLL" />
        </Typography>
        <Box />
      </ScrollLine>
    </AIHeroContainer>
  );
}
