import GradientBackground1 from 'assets/images/aiPromotion/img-gradient-bg.png';
import GradientBackground2 from 'assets/images/aiPromotion/img-gradient-background-2.png';
import { styled, Box, Link, keyframes } from '@mui/material';
import { AIPriceCardProps } from './styles.type';

const scrolldown = keyframes`
  0% { top: -20%; }
  100% { top: 120%; }
`;

const ripple = keyframes`
  0% {
    transform: translate(-50%,-50%) scale(0.8);
    opacity: 0.5;
  }
  100% {
    transform: translate(-50%,-50%) scale(2.5);
    opacity: 0;
  }
`;

export const ScrollLine = styled(Box)(() => ({
  position: 'absolute',
  left: '50%',
  bottom: 0,
  transform: 'translateX(-50%)',
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'column',
  justifyContent: 'center',
  transition: 'opacity 0.3s',

  '& p': {
    fontSize: '1.5rem',
    color: '#fff',
    marginBottom: '0.5rem',
  },

  '& div': {
    position: 'relative',
    width: '0.2rem',
    height: 'clamp(6rem, 2.5889967638vw + 5.0291262136rem, 10rem)',
    backgroundColor: 'rgba(255, 255, 255, 0.7)',
    overflow: 'hidden',
    zIndex: 2,

    '&::before': {
      content: '""',
      position: 'absolute',
      left: 0,
      top: 0,
      width: '0.2rem',
      height: 'clamp(1rem, 1.2944983819vw + 0.5145631068rem, 3rem)',
      background: 'linear-gradient(180deg, #fff 0%, #fff 100%)',
      animation: `${scrolldown} 2s ease-in infinite`,
    },
  },
}));

