export const AISliderFrame = styled(Box)(({ theme }) => ({
  width: '100%',
  position: 'absolute',
  left: '50%',
  top: '50%',
  transform: 'translate(-50%,-50%)',
  '& .swiper-wrapper': {
    transition: 'all 1s cubic-bezier(0.645, 0.045, 0.355, 1)',
  },
  '& .swiper-slide': {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },

  '& .swiper-slide-active': {
    '& img': {
      opacity: 1,
      boxShadow: 'rgba(149, 157, 165, 0.2) 0px 8px 24px',
    },
  },

  '& img': {
    width: '64%',
    opacity: '0.4',
    transition: 'ease all .3s',
    borderRadius: '2.4rem',

    [theme.breakpoints.down(1920)]: {
      width: '65%',
      borderRadius: '2rem',
    },
    [theme.breakpoints.down(1280)]: {
      width: '70%',
      borderRadius: '1.6rem',
    },
    [theme.breakpoints.down(768)]: {
      width: '68%',
      borderRadius: '1.4rem',
    },
    [theme.breakpoints.down(544)]: {
      width: '63%',
      borderRadius: '1.2rem',
    },
  },
}));

