import React from 'react';
import { FormattedMessage } from 'react-intl';

import { Box, Link, Typography } from '@mui/material';
import LogoWhite from 'assets/images/aiPromotion/img-logo-white.svg';
import { AIFooter, AISectionContainer } from '../styles';

export default function AIFooterContainer() {
  return (
    <AIFooter>
      <AISectionContainer>
        <Box>
          <Link href="#subscribe">
            <Box>
              <img src={LogoWhite} alt="Hancom Docs Logo" />
            </Box>
          </Link>
          <Typography variant="overline">
            <FormattedMessage id="FOOTER_COPYLIGHT" />
          </Typography>
        </Box>
      </AISectionContainer>
    </AIFooter>
  );
}
