import React from 'react';
import { Box, Typography } from '@mui/material';
import { FormattedMessage } from 'react-intl';

import FeatureImage1 from 'assets/images/aiPromotion/img-feature-icon-1.svg';
import FeatureImage2 from 'assets/images/aiPromotion/img-feature-icon-2.svg';

import {
  AIFeatureContainer,
  AISectionContainer,
  AITitleContainer,
  AISectionPaddingCustom,
  AIFeatureCard,
  AIContentsContainer,
} from '../styles';

const formatTags = {
  br: () => <br />,
};

export default function AIFeature() {
  return (
    <AIFeatureContainer>
      <AISectionPaddingCustom>
        <AISectionContainer>
          <AITitleContainer>
            <Box
              sx={{ display: 'flex', alignItems: 'center', gap: '1rem', justifyContent: 'center' }}
              data-aos="fade-up"
              data-aos-delay="0"
            >
              <Typography variant="h2" textAlign="center" fontWeight={700} color="#fff">
                <FormattedMessage id="FEATURE_TITLE" />
              </Typography>
              <Typography variant="h2" textAlign="center" fontWeight={700} color="#A89AFF">
                <FormattedMessage id="FEATURE_TITLE_HIGHLIGHT" />
              </Typography>
            </Box>
          </AITitleContainer>
          <AIContentsContainer>
            <Box component="ul">
              <Box component="li" data-aos="fade-up" data-aos-delay="100">
                <AIFeatureCard>
                  <Box component="article">
                    <Typography variant="h5" color="#FFF" fontWeight={700}>
                      <FormattedMessage id="FEATURE-1-TITLE" />
                    </Typography>
                    <Typography variant="subtitle2" fontWeight={400} color="rgba(255,255,255,0.8)">
                      <FormattedMessage id="FEATURE-1-DESC" values={formatTags} />
                    </Typography>
                  </Box>
                  <Box>
                    <img src={FeatureImage1} alt="" />
                  </Box>
                </AIFeatureCard>
              </Box>

              <Box component="li" data-aos="fade-up" data-aos-delay="200">
                <AIFeatureCard>
                  <Box component="article">
                    <Typography variant="h5" color="#FFF" fontWeight={700}>
                      <FormattedMessage id="FEATURE-2-TITLE" />
                    </Typography>
                    <Typography variant="subtitle2" fontWeight={400} color="rgba(255,255,255,0.8)">
                      <FormattedMessage id="FEATURE-2-DESC" values={formatTags} />
                    </Typography>
                  </Box>
                  <Box>
                    <img src={FeatureImage2} alt="" />
                  </Box>
                </AIFeatureCard>
              </Box>
            </Box>
          </AIContentsContainer>
        </AISectionContainer>
      </AISectionPaddingCustom>
    </AIFeatureContainer>
  );
}
