import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from 'swiper/modules';
import 'swiper/css';

import IphoneMockup from 'assets/images/aiPromotion/img-iphone-mockup.png';
import MockupImage1 from 'assets/images/aiPromotion/img-mockup-screen-1.png';
import MockupImage2 from 'assets/images/aiPromotion/img-mockup-screen-2.png';
import MockupImage3 from 'assets/images/aiPromotion/img-mockup-screen-3.png';
import MockupImage4 from 'assets/images/aiPromotion/img-mockup-screen-4.png';
import MockupImage5 from 'assets/images/aiPromotion/img-mockup-screen-5.png';

import { AIIphoneContainer, AIIphoneFrame, AISliderFrame } from '../styles';

const images = [MockupImage1, MockupImage5, MockupImage2, MockupImage3, MockupImage4, MockupImage5];

export default function MockupSlider() {
  return (
    <AIIphoneContainer>
      <AIIphoneFrame>
        <img src={IphoneMockup} alt="" />
      </AIIphoneFrame>
      <AISliderFrame>
        <Swiper
          modules={[Autoplay]}
          spaceBetween={0}
          slidesPerView={5}
          centeredSlides
          loop
          speed={1500}
          autoplay={{
            delay: 2500,
            disableOnInteraction: false,
          }}
          breakpoints={{
            0: {
              slidesPerView: 1.5,
            },
            544: {
              slidesPerView: 2,
            },
            768: {
              slidesPerView: 3, // 태블릿
            },
            1280: {
              slidesPerView: 4, // 태블릿
            },
            1920: {
              slidesPerView: 5, // 데스크탑
            },
          }}
        >
          {images.map((src, idx) => (
            <SwiperSlide key={idx}>
              <img src={src} alt={`Mockup ${idx + 1}`} />
            </SwiperSlide>
          ))}
        </Swiper>
      </AISliderFrame>
    </AIIphoneContainer>
  );
}
