import React from 'react';
import { Box, Typography } from '@mui/material';
import { FormattedMessage } from 'react-intl';

import DocumentEditMockup from 'assets/images/aiPromotion/img-document-edit-mockup.png';
import { AIDocumentEditContainer, AISectionContainer, AISectionPaddingMedium } from '../styles';

const formatTags = {
  br: () => <br />,
};

export default function AIDocumentEdit() {
  return (
    <AIDocumentEditContainer>
      <AISectionPaddingMedium sx={{ paddingBottom: '0 !important' }}>
        <AISectionContainer sx={{ maxWidth: '192rem' }}>
          <Box component="ul">
            <Box component="li" key="document-title">
              <Typography variant="h6" fontWeight={600} color="#8070E4" data-aos="fade-up" data-aos-delay="0">
                <FormattedMessage id="DOCUMENT-EDIT-TITLE" />
              </Typography>
              <Typography variant="h4" fontWeight={700} color="#000" data-aos="fade-up" data-aos-delay="100">
                <FormattedMessage id="DOCUMENT-EDIT-HEADING" values={formatTags} />
              </Typography>
              <Typography
                variant="subtitle2"
                component="p"
                fontWeight={400}
                color="#333"
                data-aos="fade-up"
                data-aos-delay="200"
              >
                <FormattedMessage id="DOCUMENT-EDIT-DESCRIPTION" values={formatTags} />
              </Typography>
            </Box>
            <Box component="li" data-aos="fade-right" data-aos-delay="300" key="document-image">
              <img src={DocumentEditMockup} alt="" />
            </Box>
          </Box>
        </AISectionContainer>
      </AISectionPaddingMedium>
    </AIDocumentEditContainer>
  );
}
