import React, { useRef, useEffect, useState } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { ThemeProvider } from '@mui/material/styles';
import { CssBaseline, GlobalStyles } from '@mui/material';

import darkTheme from './theme';
import AIHeader from './components/AIHeader';
import AIHero from './components/AIHero';
import AIBrandStory from './components/AIBrandStory';
import AIBrandVideo from './components/AIBrandVideo';
import AIRefinder from './components/AIRefinder';
import AIDocumentEdit from './components/AIDocumentEdit';
import AIVarientDocument from './components/AIVarientDocument';
import AIFeature from './components/AIFeature';
import AIPrice from './components/AIPrice';
import AIFaq from './components/AIFaq';
import AIContactUs from './components/AIContactUs';
import ASAside from './components/AIAside';
import AIFooterContainer from './components/AIFooterContainer';
// import AIIntro from './components/AIIntro';

import { AIBackgroundInvert } from './styles';

const AIView = () => {
  const headerRef = useRef<HTMLElement>(null);
  const asideRef = useRef<HTMLDivElement>(null);
  // const [isLoaded, setIsLoaded] = useState(false);

  const invertRef1 = useRef<HTMLDivElement>(null);
  const invertRef2 = useRef<HTMLDivElement>(null);
  const [isInverted, setIsInverted] = useState(false);

  // 로딩
  // useEffect(() => {
  //   const loadApp = async () => {
  //     await document.fonts.ready;
  //     requestAnimationFrame(() => setIsLoaded(true));
  //   };

  //   if (document.readyState === 'complete') {
  //     loadApp();
  //   } else {
  //     const onLoad = () => {
  //       loadApp();
  //       window.removeEventListener('load', onLoad);
  //     };
  //     window.addEventListener('load', onLoad);
  //   }
  // }, []);
  // AOS
  useEffect(() => {
    AOS.init({
      duration: 800,
      once: true,
    });
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      const header = headerRef.current;
      const aside = asideRef.current;
      if (!header || !aside) return;

      const asideRect = aside.getBoundingClientRect();

      const invertRects = [invertRef1.current, invertRef2.current]
        .filter(Boolean)
        .map((el) => el!.getBoundingClientRect());

      const isAnyOverlapping = invertRects.some((rect) => asideRect.bottom > rect.top && asideRect.top < rect.bottom);

      setIsInverted(isAnyOverlapping);
    };

    window.addEventListener('scroll', handleScroll);
    handleScroll();

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  // if (!isLoaded) return <AIIntro />;

  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <GlobalStyles
        styles={{
          html: { fontSize: '62.5%', wordBreak: 'keep-all', scrollBehavior: 'smooth' },
          '.inter': {
            fontFamily: `'Inter', sans-serif !important`,
          },

          'ul, ol, li': {
            listStyle: 'none',
            margin: 0,
            padding: 0,
          },
          '@media (max-width:768px)': {
            br: {
              display: 'none',
            },
          },
        }}
      />

      {/* CONTENTS BEGIN */}
      <AIHeader ref={headerRef} isInverted={isInverted} />
      <AIHero />
      <AIBrandStory />
      <AIBrandVideo />

      {/* BACKGROUND-INVERT-BEGIN */}
      <AIBackgroundInvert ref={invertRef1}>
        <AIRefinder />
        <AIDocumentEdit />
        <AIVarientDocument />
      </AIBackgroundInvert>
      <AIFeature />
      <AIBackgroundInvert ref={invertRef2}>
        <AIPrice />
        <AIFaq />
      </AIBackgroundInvert>
      {/* BACKGROUND-INVERT-END */}

      <AIContactUs />
      <ASAside ref={asideRef} isInverted={isInverted} />
      {/* CONTENTS END */}
      <AIFooterContainer />
    </ThemeProvider>
  );
};

export default AIView;
