import React from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { Box, Typography } from '@mui/material';
import CreditCard from 'assets/images/aiPromotion/img-credit-card.png';

import PriceIconPersonal from 'assets/images/aiPromotion/icons/img-personal-icon.svg';
import PriceIconBusiness from 'assets/images/aiPromotion/icons/img-business-icon.svg';
import LogoSymbol from 'assets/images/aiPromotion/icons/img-logo-symbol.svg';
import {
  AIPriceCard,
  AIPriceCardHeader,
  AIPriceCardContent,
  AIPriceTag,
  AIPriceMeta,
  AIPriceDetail,
  AIPriceTitle,
  AIPriceContainer,
  AISectionPaddingLarge,
  AISectionContainer,
  AITitleContainer,
  AIButtonWrap,
  AIButton,
  AIContentsContainer,
  AICreditBanner,
  AIGradientBadge,
} from '../styles';

import useCountUp from '../hooks/useCountUp';

const formatTags = {
  br: () => <br />,
};

export default function AIPrice() {
  const intl = useIntl();

  // 개인용 가격
  const personalText = intl.formatMessage({ id: 'PRICE_PERSONAL_MONTHLY' });
  const personalNumber = Number(personalText.replace(/[^\d]/g, ''));
  const { ref: personalRef, value: personalMonthly } = useCountUp(personalNumber, 1200);

  // 기업용 가격
  const businessText = intl.formatMessage({ id: 'PRICE_BUSINESS_MONTHLY' });
  const businessNumber = Number(businessText.replace(/[^\d]/g, ''));
  const { ref: businessRef, value: businessMonthly } = useCountUp(businessNumber, 1200);

  return (
    <AIPriceContainer>
      <AISectionPaddingLarge>
        <AISectionContainer>
          <AITitleContainer>
            <Typography
              variant="h2"
              textAlign="center"
              fontWeight={700}
              color="#8070E4"
              data-aos="fade-up"
              data-aos-delay="0"
            >
              <FormattedMessage id="PRICE_TITLE_1" />
            </Typography>
            <Typography
              variant="h2"
              textAlign="center"
              fontWeight={700}
              color="#000"
              data-aos="fade-up"
              data-aos-delay="100"
            >
              <FormattedMessage id="PRICE_TITLE_2" />
            </Typography>
            <Typography
              variant="subtitle2"
              fontWeight={500}
              color="#333"
              textAlign="center"
              data-aos="fade-up"
              data-aos-delay="200"
            >
              <FormattedMessage id="PRICE_DESCRIPTION" values={formatTags} />
            </Typography>
          </AITitleContainer>

          <AIButtonWrap data-aos="fade-up" data-aos-delay="300">
            <AIButton href="https://www.hancomdocs.com/ko/plans" target="_blank" rel="noopener noreferrer">
              <Typography variant="caption" fontWeight={500}>
                <FormattedMessage id="PRICE_BUTTON" />
              </Typography>
              <FontAwesomeIcon icon={faArrowRight} />
            </AIButton>
          </AIButtonWrap>

          <AIContentsContainer>
            <Box component="ul">
              <Box component="li" data-aos="fade-up" data-aos-delay="400">
                {/* 개인용 카드 */}
                <AIPriceCard colorType="blue">
                  <AIPriceCardHeader colorType="blue">
                    <Typography variant="body1" color="#2F78FF" fontWeight={500}>
                      <FormattedMessage id="PRICE_PERSONAL_LABEL" />
                    </Typography>
                  </AIPriceCardHeader>
                  <AIPriceCardContent>
                    <AIPriceMeta>
                      <AIPriceTitle>
                        <img src={PriceIconPersonal} alt="" />
                        <Typography variant="h6" color="#2F78FF" fontWeight={600}>
                          <FormattedMessage id="PRICE_PERSONAL_TITLE" />
                        </Typography>
                      </AIPriceTitle>
                      <AIPriceTag colorType="blue">
                        <Typography variant="body2" fontWeight={600}>
                          <FormattedMessage id="PRICE_PERSONAL_DISCOUNT" />
                        </Typography>
                      </AIPriceTag>
                    </AIPriceMeta>
                    <AIPriceDetail>
                      <Box>
                        <Typography ref={personalRef} color="#000" variant="h1" fontWeight={700}>
                          ₩{personalMonthly.toLocaleString()}
                        </Typography>
                        <Typography
                          color="#000"
                          variant="subtitle2"
                          fontWeight={400}
                          sx={{ transform: 'translateY(-5px)' }}
                        >
                          <FormattedMessage id="PRICE_MONTH_LABEL" />
                        </Typography>
                      </Box>
                      <Typography color="#666" sx={{ textDecoration: 'line-through' }}>
                        <FormattedMessage id="PRICE_PERSONAL_ORIGINAL" />
                      </Typography>
                    </AIPriceDetail>
                  </AIPriceCardContent>
                </AIPriceCard>
              </Box>

              <Box component="li" data-aos="fade-up" data-aos-delay="500">
                {/* 기업용 카드 */}
                <AIPriceCard colorType="purple">
                  <AIPriceCardHeader colorType="purple">
                    <Typography variant="body1" color="#8070E4" fontWeight={500}>
                      <FormattedMessage id="PRICE_BUSINESS_LABEL" />
                    </Typography>
                  </AIPriceCardHeader>
                  <AIPriceCardContent>
                    <AIPriceMeta>
                      <AIPriceTitle>
                        <img src={PriceIconBusiness} alt="" />
                        <Typography variant="h6" color="#8070E4" fontWeight={600}>
                          <FormattedMessage id="PRICE_BUSINESS_TITLE" />
                        </Typography>
                      </AIPriceTitle>
                      <AIPriceTag colorType="purple">
                        <Typography variant="body2" fontWeight={600}>
                          <FormattedMessage id="PRICE_BUSINESS_DISCOUNT" />
                        </Typography>
                      </AIPriceTag>
                    </AIPriceMeta>
                    <AIPriceDetail>
                      <Box>
                        <Typography ref={businessRef} color="#000" variant="h1" fontWeight={700}>
                          ₩{businessMonthly.toLocaleString()}
                        </Typography>
                        <Typography
                          color="#000"
                          variant="subtitle2"
                          fontWeight={400}
                          sx={{ transform: 'translateY(-5px)' }}
                        >
                          <FormattedMessage id="PRICE_MONTH_LABEL" />
                        </Typography>
                      </Box>
                      <Typography color="#666" sx={{ textDecoration: 'line-through' }}>
                        <FormattedMessage id="PRICE_BUSINESS_ORIGINAL" />
                      </Typography>
                    </AIPriceDetail>
                  </AIPriceCardContent>
                </AIPriceCard>
              </Box>
            </Box>

            <AICreditBanner data-aos="fade-up" data-aos-delay="600">
              <Box>
                <AIGradientBadge>
                  <Typography variant="caption" fontWeight={500} color="#fff">
                    <FormattedMessage id="PRICE_CREDIT_BADGE" />
                  </Typography>
                </AIGradientBadge>
                <Box sx={{ position: 'relative' }}>
                  <Box component="figure">
                    <img src={LogoSymbol} alt="" />
                  </Box>
                  <Box component="hgroup">
                    <Typography variant="h5" fontWeight={700} color="#8070E4">
                      <FormattedMessage id="PRICE_CREDIT_AMOUNT" />
                    </Typography>
                    <Typography variant="h5" fontWeight={500} color="#444444">
                      <FormattedMessage id="PRICE_CREDIT_LABEL" />
                    </Typography>
                  </Box>
                </Box>
              </Box>
              <Box component="picture">
                <img src={CreditCard} alt="" />
              </Box>
            </AICreditBanner>
          </AIContentsContainer>
        </AISectionContainer>
      </AISectionPaddingLarge>
    </AIPriceContainer>
  );
}
