import React, { forwardRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowUp } from '@fortawesome/free-solid-svg-icons';
import { Box, Typography } from '@mui/material';

import LogoSymbol from 'assets/images/aiPromotion/icons/img-symbol.svg';
import { AIAsideButtonWrap, AITopButton, AISubscribeButton } from '../styles';

type Props = {
  isInverted?: boolean;
};

const ASAside = forwardRef<HTMLDivElement, Props>(({ isInverted = false }, ref) => {
  const handleClick = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  return (
    <AIAsideButtonWrap>
      <AITopButton ref={ref} onClick={handleClick} className={isInverted ? 'inverted' : ''}>
        <FontAwesomeIcon icon={faArrowUp} />
      </AITopButton>
      <AISubscribeButton href="https://www.hancomdocs.com/ko/plans" target="_blank" rel="noopener noreferrer">
        <Box>
          <img src={LogoSymbol} alt="" />
        </Box>
        <Typography>구독하기</Typography>
      </AISubscribeButton>
    </AIAsideButtonWrap>
  );
});

export default ASAside;
