import React, { useRef, useEffect, useState } from 'react';
import Spline from '@splinetool/react-spline';
import { Box, Typography, Link } from '@mui/material';
import { AIContactContainer, AIRippleCircle, AIRippleWrapper, AIMouseCursor } from '../styles';

export default function AIContactUs() {
  const [showCursor, setShowCursor] = useState(false);
  const [mousePos, setMousePos] = useState({ x: 0, y: 0 });
  const cursorRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!showCursor) {
      return undefined;
    }

    const moveCursor = (e: MouseEvent) => {
      setMousePos({ x: e.clientX, y: e.clientY });
    };

    window.addEventListener('mousemove', moveCursor);
    return () => {
      window.removeEventListener('mousemove', moveCursor);
    };
  }, [showCursor]);

  return (
    <AIContactContainer>
      <Box>
        <Box component="article">
          <Link
            href="https://www.hancomdocs.com/ko/plans"
            underline="none"
            sx={{ color: '#fff', textDecoration: 'none' }}
            onMouseEnter={() => setShowCursor(true)}
            onMouseLeave={() => setShowCursor(false)}
          >
            <Typography>Get In Touch</Typography>
          </Link>
        </Box>
        <Spline scene="https://prod.spline.design/CzPDFvYseEpvOfnZ/scene.splinecode" />
      </Box>

      <AIRippleWrapper>
        {[0, 1, 2, 3].map((delay, idx) => (
          <AIRippleCircle key={idx} delay={delay} />
        ))}
      </AIRippleWrapper>

      <AIMouseCursor
        ref={cursorRef}
        className={showCursor ? 'active' : ''}
        style={{
          top: `${mousePos.y}px`,
          left: `${mousePos.x}px`,
        }}
      >
        Contact Us
      </AIMouseCursor>
    </AIContactContainer>
  );
}
