import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { FormattedMessage } from 'react-intl';
import { Accordion, AccordionSummary, AccordionDetails, Typography, Box } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

import {
  AISectionPaddingLarge,
  AISectionContainer,
  AITitleContainer,
  AIButtonWrap,
  AIButtonText,
  AIContentsContainer,
  AIFaqContainer,
} from '../styles';

export default function AIFaq() {
  const [expandedIndex, setExpandedIndex] = useState<number | null>(null);

  return (
    <AIFaqContainer>
      <AISectionPaddingLarge>
        <AISectionContainer>
          <AITitleContainer data-aos="fade-up" data-aos-delay="200">
            <Typography variant="h2" textAlign="center" fontWeight={700} color="#000">
              자주 묻는 질문
            </Typography>
          </AITitleContainer>
          <AIContentsContainer data-aos="fade-up" data-aos-delay="300">
            <Box sx={{ borderTop: '1px solid #000' }}>
              {[0, 1, 2].map((index) => (
                <Accordion
                  key={index}
                  disableGutters
                  elevation={0}
                  square
                  sx={{
                    border: 'none',
                    boxShadow: 'none',
                    background: 'none',
                    margin: 0,
                    padding: '2rem 0',
                    borderBottom: '1px solid #eee',
                    '&::before': { display: 'none' },
                    '&.Mui-expanded': {
                      margin: 0,
                    },
                  }}
                >
                  <AccordionSummary
                    expandIcon={
                      <Box sx={{ color: '#000' }}>
                        {expandedIndex === index ? (
                          <RemoveIcon sx={{ fontSize: '3rem' }} />
                        ) : (
                          <AddIcon sx={{ fontSize: '3rem' }} />
                        )}
                      </Box>
                    }
                    sx={{
                      padding: 0,
                      margin: 0,
                      '&.Mui-expanded': {
                        margin: 0,
                      },
                      '& .MuiAccordionSummary-content.Mui-expanded': {
                        margin: 0,
                      },
                    }}
                    onClick={() => setExpandedIndex(expandedIndex === index ? null : index)}
                  >
                    <Typography variant="subtitle1" fontWeight={600} color="#000">
                      <FormattedMessage id={`FAQ_ITEM_${index + 1}_Q`} />
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails
                    sx={{
                      padding: 0,
                      marginTop: '2.4rem',
                    }}
                  >
                    <Typography variant="caption" whiteSpace="pre-line" color="#333">
                      <FormattedMessage id={`FAQ_ITEM_${index + 1}_A`} />
                    </Typography>
                  </AccordionDetails>
                </Accordion>
              ))}
            </Box>

            <AIButtonWrap>
              <AIButtonText href="https://support.hancomdocs.com/faq" target="_blank" rel="noopener noreferrer">
                <Typography variant="caption" fontWeight={500}>
                  <FormattedMessage id="LINK_TITLE_VIEW_MORE" />
                </Typography>
                <FontAwesomeIcon icon={faArrowRight} />
              </AIButtonText>
            </AIButtonWrap>
          </AIContentsContainer>
        </AISectionContainer>
      </AISectionPaddingLarge>
    </AIFaqContainer>
  );
}
